body {
  min-width: 320px;
  overflow-x: hidden;
  background-color: $basic-black;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

input {
  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.page {
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;

  font-family: "Roboto", "Arial", sans-serif;
  color: $basic-white;
  background-color: $basic-black;
}

.content {
  flex: 1 0 auto;
  background-image: url("../img/body-bg.webp");
  background-repeat: repeat-y;
  background-position: center;
}
