.page-header {
  z-index: 3;

  padding: 20px 0 0;

  background-color: $basic-black;
}

.main-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header__logo {
  display: flex;
  align-items: top;
  width: auto;
  margin-right: 60px;

  text-decoration: none;

  & img {
    margin-right: 15px;
  }
}

.site-list__link {
  margin-right: 30px;
  padding-bottom: 10px;

  font-weight: 600;
  font-size: 14px;
  font-family: "Panton", "Arial", sans-serif;
  color: $basic-light-grey;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    color: $basic-white;

    border-bottom: 1px solid $basic-purple;
  }
}

.page-header__logo-text {
  position: relative;

  margin-top: 8px;
  padding-left: 15px;

  font-size: 14px;
  color: $basic-light-grey;
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;

    width: 1px;
    height: 24px;

    background-color: $basic-light-grey;
  }
}

.phone-numbers {
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  margin-right: 14px;
  padding-left: 26px;

  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 20px 2px;
}

.phone-numbers__item {
  margin-bottom: 8px;

  font-weight: 500;
  font-size: 16px;
  text-align: center;
  color: $basic-white;
  text-decoration: none;

  &:hover {
    font-weight: 500;
    color: $basic-purple;
  }
}

.btn-yellow {
  margin-top: 10px;
  padding: 10px 30px 10px;

  font-weight: 500;
  font-size: 14px;
  text-align: center;
  color: $special-black;
  text-decoration: none;

  background: linear-gradient(180deg, #fed468 0%, #fecd50 100%);
  border-radius: 40px;
  box-shadow: 0 2px 15px 2px rgba(254, 212, 104, 0.2);

  &:hover {
    color: $special-black;

    opacity: 0.9;
  }
}

.main-nav__list {
  @include reset-list;

  display: flex;
}

@media screen and (max-width: 1199px) {
  .page-header__logo {
    width: 30%;
    margin-right: 0;
  }
}

@media screen and (max-width: 1099px) {
  .page-header__logo {
    width: 32%;
    margin-right: 0;
  }

  .site-list__link {
    margin-right: 20px;

    font-size: 13px;
  }
}

@media screen and (max-width: 991px) {
  .page-header__logo {
    width: 50%;
    margin-right: 0;
  }

  .main-nav__list {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .page-header__logo {
    width: 60%;
    margin-right: 0;
  }
}

@media screen and (max-width: 575px) {
  .phone-numbers {
    display: none;
  }

  .page-header__logo-text {
    font-size: 12px;
  }

  .page-header__logo {
    width: 90%;
  }
}
