.about-me {
  display: grid;
  margin-top: 80px;

  background-color: $special-black;
  background-image: url("../img/loupe.png");
  background-repeat: no-repeat;
  background-position: 96% 96%;

  grid-template-columns: 445px 1fr;
}

.about-me__wrapper {
  padding: 40px 30px 0;
}

.about-me__title {
  margin-bottom: 20px;

  font-weight: 700;
  font-size: 28px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
  word-spacing: 4px;
}

.about-me__text {
  color: $basic-light-grey;
}

.btn-about-me {
  display: inline-block;
  margin-top: 35px;
  margin-bottom: 0;
  padding: 20px 44px;

  font-size: 14px;
  text-align: center;
  color: $basic-white;
  text-decoration: none;

  background: linear-gradient(180deg, #8627e5 0%, #7821cf 100%);
  border: none;
  border-radius: 40px;
  box-shadow: 0 2px 15px 2px rgba(134, 39, 229, 0.2);

  &:hover {
    color: $basic-white;

    opacity: 0.8;
  }
}

@media screen and (max-width: 991px) {
  .about-me {
    display: flex;
    flex-direction: column;
  }

  .about-me__wrapper {
    padding-top: 10px;
    padding-bottom: 40px;
  }
}

@media screen and (max-width: 767px) {
  .about-me__title {
    font-size: 24px;
  }
}

@media screen and (max-width: 575px) {
  .about-me {
    background-image: none;
  }

  .about-me__wrapper {
    padding: 30px 20px;
  }

  .btn-about-me {
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    padding: 20px 22px;

    font-size: 12px;
  }

  .about-me__title {
    margin-top: 0;
    margin-bottom: 5px;

    font-size: 18px;
  }

  .about-me__text {
    font-size: 14px;
  }
}
