.steps {
  margin-top: 80px;
}

.steps__list {
  @include reset-list;

  display: grid;

  grid-column-gap: 20px;
  grid-row-gap: 20px;
  grid-template-columns: 1fr 1fr 1fr;
}

.steps__list-item {
  padding: 20px;
  padding-top: 60px;
  padding-bottom: 0;

  background-color: $special-black;
  background-repeat: no-repeat;
  background-position: 15px 15px;
}

.steps__list-item-header {
  margin-top: 0;
  margin-left: 10px;

  font-weight: 700;
  font-size: 20px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
  word-spacing: 4px;
}

.steps__list-item-text {
  color: $basic-light-grey;
  padding-bottom: 20px;
  padding-left: 70px;
}

.steps__list-item--first {
  background-image: url("../img/one.svg");

  & p {
    background-image: url("../img/request.svg");
    background-repeat: no-repeat;
    background-position: left top;
  }
}

.steps__list-item--second {
  background-image: url("../img/two.svg");

  & p {
    background-image: url("../img/consulting.svg");
    background-repeat: no-repeat;
    background-position: left top;
  }
}

.steps__list-item--third {
  background-image: url("../img/three.svg");

  & p {
    background-image: url("../img/tasks.svg");
    background-repeat: no-repeat;
    background-position: left top;
  }
}

.steps__list-item--fourth {
  background-image: url("../img/four.svg");

  & p {
    background-image: url("../img/guarante.svg");
    background-repeat: no-repeat;
    background-position: left top;
  }
}

.steps__list-item--fifth {
  background-image: url("../img/five.svg");

  & p {
    background-image: url("../img/documents.svg");
    background-repeat: no-repeat;
    background-position: left top;
  }
}

.steps__list-item--sixth {
  background-image: url("../img/six.svg");

  & p {
    background-image: url("../img/files.svg");
    background-repeat: no-repeat;
    background-position: left top;
  }
}

@media screen and (max-width: 991px) {
  .steps__list {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 767px) {
  .steps__list {
    grid-template-columns: 1fr;
  }

  .steps__list-item-text {
    margin: 0;
    padding: 20px 0 20px 70px;
  }
}

@media screen and (max-width: 575px) {
  .steps__list-item {
    padding: 60px 10px 5px;
  }

  .steps__list-item-header {
    margin-bottom: 8px;
  }

  .steps__list-item-text {
    padding-top: 0;

    font-size: 14px;
  }
}
