.directions {
  padding-top: 80px;
}

.directions__subtitle {
  @include subtitle;
}

.directions__title {
  @include title;

  margin: 0;
  margin-top: 20px;
  margin-bottom: 50px;
}

.directions__list {
  @include reset-list;

  display: grid;
  margin-top: 50px;

  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 290px 290px;
}

.directions__list-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-height: 290px;
  padding: 0 20px 15px;

  background-repeat: no-repeat;
  background-size: cover;
}

.directions__list-item-header {
  margin-bottom: 0;

  font-weight: 600;
  font-size: 22px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
}

.directions__list-item-description {
  font-size: 16px;
  color: $basic-light-grey;
}

.directions__list-item--business {
  background-image: url("../img/business.webp");
}

.directions__list-item--love {
  background-image: url("../img/love.webp");
}

.directions__list-item--research {
  background-image: url("../img/research.webp");

  grid-column: 3/4;
  grid-row: 1/3;
}

.directions__list-item--spy {
  background-image: url("../img/spy.webp");
}

.directions__list-item--family {
  background-image: url("../img/family.webp");
}

@media screen and (max-width: 991px) {
  .directions__list {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 290px 290px 290px;
  }

  .directions__list-item--family {
    grid-column: 1/4;
    grid-row: 1/1;
  }

  .directions__list-item--research {
    background-image: url("../img/research.webp");

    grid-column: 2/4;
    grid-row: 2/2;
  }

  .directions__title {
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 575px) {
  .directions__subtitle {
    width: 100%;

    font-size: 14px;
  }

  .directions__title {
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 575px) {
  .directions {
    padding-top: 60px;
  }

  .directions__list {
    grid-template-columns: 1fr;
  }

  .directions__list-item--family {
    grid-column: 1/1;
    grid-row: 1/1;
  }

  .directions__list-item--research {
    grid-column: 1/1;
    grid-row: 2/2;
  }

  .directions__list-item {
    min-height: 290px;
  }
}
