.services {
  margin-top: 80px;
}

.services__list {
  @include reset-list;

  margin-top: 50px;

  background-color: $special-black;
}

.services__list-item {
  display: grid;
  padding-right: 30px;

  border-bottom: 1px solid $basic-grey;

  grid-column-gap: 40px;
  grid-template-columns: 255px 1fr;

  &:last-child {
    border-bottom: none;
  }
}

.services__list-item-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px 0 20px;
}

.services__list-item-header {
  margin: 0;
  margin-bottom: 15px;

  font-weight: 700;
  font-size: 22px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
  word-spacing: 10px;
}

.item-list {
  @include reset-list;
}

.item-list__item {
  position: relative;

  margin-bottom: 8px;
  padding-left: 20px;

  font-size: 16px;
  color: $basic-light-grey;

  &::before {
    content: "";
    position: absolute;
    top: 9px;
    left: 0;

    width: 4px;
    height: 4px;

    background-color: $basic-yellow;
    border-radius: 100%;
  }
}

.services__list-item-link {
  font-size: 16px;
  color: $basic-purple;
  text-decoration: none;

  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: 200px center;

  &:hover {
    color: $basic-purple;

    opacity: 0.7;
  }
}

@media screen and (max-width: 767px) {
  .services__list-item {
    display: flex;
    flex-direction: column;
    padding-right: 0;
  }

  .services__list-item-wrapper {
    padding: 30px;
  }

  .services__list-item-link {
    margin-top: 30px;
  }
}

@media screen and (max-width: 575px) {
  .services__list-item-wrapper {
    padding: 20px;
  }

  .services__list-item-header {
    font-size: 18px;
  }

  .item-list__item {
    font-size: 14px;
  }
}
