.consultation {
  margin-top: 50px;
  padding: 60px 40px 0;

  background-color: $special-black;
  background-image: url("../img/detective2.webp");
  background-repeat: no-repeat;
  background-position: bottom right;
}

.consultation__wrapper {
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
}

.consultation__header,
.consultation__header-phone {
  font-weight: 600;
  font-size: 16px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
  word-spacing: 5px;

  &:last-child {
    text-align: right;
  }
}

.consultation__header-phone {
  text-align: right;
}

.consultation__input {
  width: 280px;
  margin-bottom: 16px;
  padding-bottom: 8px;

  color: $basic-white;

  background-color: transparent;
  border: none;
  border-bottom: 1px solid $basic-light-grey;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    border-bottom: 1px solid $basic-purple;
    outline: none;
    opacity: 0.7;
  }
}

.consultation__wrapper .phone-numbers {
  margin-right: 0;
}

.btn-consult {
  display: inline-block;
  margin-bottom: 50px;
  padding: 20px 44px;

  font-size: 14px;
  color: $basic-white;
  text-decoration: none;

  background: linear-gradient(180deg, #8627e5 0%, #7821cf 100%);
  border: none;
  border-radius: 40px;
  box-shadow: 0 2px 15px 2px rgba(134, 39, 229, 0.2);
}

.phone-numbers-consult {
  display: flex;
  flex-direction: column;
  margin-right: 0;

  text-align: right;

  background-image: url("../img/icon-phone-yellow.svg");
  background-repeat: no-repeat;
  background-position: 50px 3px;

  & .phone-numbers__item {
    text-align: right;
  }
}

@media screen and (max-width: 767px) {
  .consultation__wrapper {
    flex-direction: column;
    justify-content: center;
  }

  .consultation__header-phone {
    text-align: left;
  }

  .phone-numbers-consult {
    padding-bottom: 40px;

    background-position: left 0;

    & .phone-numbers__item {
      padding-left: 28px;

      text-align: left;
    }
  }

  .btn-consult {
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 575px) {
  .consultation {
    padding: 40px 20px;

    background-image: url("../img/detective2-mobile.webp");
    background-position: right bottom;
  }

  .directions__title {
    font-size: 30px;
  }

  .phone-numbers-consult {
    padding-bottom: 0;
  }

  .consultation__input {
    width: 100%;
  }
}
