.page-footer {
  margin-top: 80px;
  padding: 30px 0 20px;

  background-color: $special-black;
}

.footer__logo {
  display: flex;
  align-items: top;

  & img {
    margin-right: 15px;
  }
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.page-footer__copyright {
  margin: 0;

  font-size: 14px;
  text-align: center;
  color: $basic-light-grey;
}

.phone-numbers-footer {
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  margin-right: 14px;
  padding-left: 26px;

  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 3px 2px;
}

@media screen and (max-width: 991px) {
  .footer__logo .page-header__logo-text {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    flex-wrap: wrap;
  }

  .footer__logo {
    display: flex;
    align-items: top;
    order: 1;
    width: auto;

    & img {
      margin-right: 15px;
    }
  }

  .phone-numbers-footer {
    order: 2;
  }

  .footer__logo .page-header__logo-text {
    display: inline-block;
  }

  .page-footer__copyright {
    display: block;
    order: 3;
    width: 100%;
    margin-top: 30px;

    text-align: center;
  }
}

@media screen and (max-width: 575px) {
  .footer {
    flex-direction: column;
  }

  .footer__logo {
    margin-bottom: 20px;
  }
}
