@mixin reset-list {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin title {
  font-size: 36px;
  font-weight: 800;
  font-family: "Panton", "Arial", sans-serif;
  word-spacing: 4px;
  text-align: center;
  text-transform: uppercase;

  & span {
    color: $basic-purple;
  }
}

@mixin subtitle {
  font-size: 16px;
  font-weight: 400;
  color: $basic-light-grey;
  text-align: center;

  & span {
    color: $basic-white;
    font-weight: 600;
  }
}
