.guarantee {
  margin-top: 80px;
}

.guarantee__wrapper {
  display: grid;
  margin-top: 50px;

  grid-column-gap: 30px;
  grid-template-columns: 1fr 350px;
}

.guarantee__list {
  @include reset-list;

  display: grid;

  grid-column-gap: 20px;
  grid-template-columns: 1fr 1fr;
}

.guarantee__list-item-header {
  font-weight: 600;
  font-size: 18px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
  word-spacing: 4px;
}

.guarantee__list-item-text {
  margin-top: 8px;

  font-size: 16px;
  line-height: 24px;
  color: $basic-light-grey;
}

.guarantee__list-item {
  padding-left: 90px;

  background-repeat: no-repeat;
  background-position: left top;
}

.guarantee__list-item--license {
  background-image: url("../img/license.svg");
}

.guarantee__list-item--file {
  background-image: url("../img/file.svg");
}

.guarantee__list-item--lock {
  background-image: url("../img/lock.svg");
}

.guarantee__list-item--clock {
  background-image: url("../img/clock.svg");
}

.guarantee__list-item--guarantee {
  background-image: url("../img/guarantee.svg");
}

.guarantee__list-item--incognito {
  background-image: url("../img/incognito.svg");
}

@media screen and (max-width: 991px) {
  .guarantee__wrapper {
    display: flex;
    flex-direction: column;
  }

  .guarantee__list {
    order: 2;
    margin-top: 30px;
  }

  .guarantee__img {
    display: block;
    margin: 0 auto;
  }
}

@media screen and (max-width: 767px) {
  .guarantee {
    margin-top: 60px;
  }

  .guarantee__list {
    grid-row-gap: 20px;
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 575px) {
  .guarantee__list-item-header {
    margin: 5px;
  }

  .guarantee__list-item-text {
    font-size: 14px;
  }

  .guarantee__list-item {
    padding-top: 80px;
    padding-left: 0;
  }
}
