.container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;

  @media (min-width: 576px) {
    width: 540px;
    padding: 0 18px;
  }

  @media (min-width: 768px) {
    width: 720px;
    padding: 0 24px;
  }

  @media (min-width: 992px) {
    width: 960px;
    padding: 0 16px;
  }

  @media (min-width: 1100px) {
    width: 1060px;
    padding: 0 20px;
  }

  @media (min-width: 1200px) {
    width: 1140px;
    padding: 0 30px;
  }

  @media (min-width: 1400px) {
    width: 1320px;
    padding: 0 40px;
  }

  @media (min-width: 1660px) {
    width: 1600px;
    padding: 0 30px;
  }
}
