.modal-close {
  display: none;
}

.modal__call-me {
  position: relative;
  background-color: $special-black;
  margin: auto;
  width: 600px;
  padding: 30px;
  background-image: url("../img/modal-img.png");
  background-repeat: no-repeat;
  background-position: right bottom;

  & .btn-consult {
    margin-bottom: 0;
  }
}

.modal__call-me-header {
  font-size: 28px;
  text-transform: uppercase;
  font-family: "Panton", "Arial", sans-serif;
  word-spacing: 4px;
  font-weight: 700;
  margin: 0;

  & span {
    color: $basic-purple;
  }
}

.modal__call-me-subheader {
  color: $basic-light-grey;
  margin-bottom: 50px;
}

.btn-close {
  position: absolute;
  top: 30px;
  right: 30px;
  background-image: url("../img/modal-close.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  background-color: transparent;
  border: none;

  &:hover {
    opacity: 0.6;
  }

  &:focus {
    opacity: 0.6;
    outline: none;
  }
}

@media screen and (max-width: 767px) {
  .modal__call-me {
    width: 80%;
    padding: 30px;
    background-size: 50%;
  }

  .modal__call-me-subheader {
    width: 60%;
  }
}

@media screen and (max-width: 575px) {
  .modal__call-me {
    width: 100%;
    padding: 30px;
    background-image: none;

    & .consultation__input {
      width: 100%;
    }
  }

  .modal__call-me-header {
    font-size: 22px;
  }
}
