.main-screen {
  margin-bottom: 0;

  background-color: $basic-black;
  background-image: url("../img/main-bg-text.webp"), url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center 40%, center;
}

.slider__item {
  position: relative;

  display: flex;
  flex-direction: column;
}

.slider__item-title {
  margin-top: 60px;

  font-weight: 700;
  font-size: 28px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
  word-spacing: 4px;


  & span {
    color: $basic-purple;
  }
}

.slider__item-subtitle {
  font-size: 16px;
  line-height: 22px;
  color: $basic-light-grey;
}

.slider__item-name {
  margin-bottom: 60px;

  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  font-family: "Panton", "Arial", sans-serif;
  color: $basic-purple;
  text-transform: uppercase;
  word-spacing: 4px;

  & span {
    font-size: 16px;
    color: $basic-light-grey;
    text-transform: none;
  }
}

.slider__item-link {
  padding-right: 30px;

  font-weight: 500;
  font-size: 16px;
  color: $basic-purple;

  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: right center;
}

.arrow-left {
  position: absolute;
  top: 50%;
  left: -60px;

  width: 30px;
  height: 30px;

  background-color: transparent;
  background-image: url("../img/right-arrow.svg");
  background-repeat: no-repeat;
  border: none;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.arrow-right {
  position: absolute;
  top: 50%;
  right: -60px;

  width: 30px;
  height: 30px;

  background-color: transparent;
  background-image: url("../img/left-arrow.svg");
  background-repeat: no-repeat;
  border: none;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.slider-nav {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.slider__item-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 60%;
  min-height: 550px;
}

.first-slide {
  background-image: url("../img/detective.webp");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: auto;
}

.second-slide {
  background-image: url("../img/second-slide.png");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: auto;
}

.third-slide {
  background-image: url("../img/third-slide.png");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: auto;
}

.fourth-slide {
  background-image: url("../img/fourth-slide.png");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: auto;
}

.fifth-slide {
  background-image: url("../img/fifth-slide.png");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: auto;
}

.sixth-slide {
  background-image: url("../img/sixth-slide.png");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: auto;
}

.slide-toggler {
  width: 14px;
  height: 3px;
  margin-right: 10px;

  background-color: $basic-light-grey;
  border: none;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.slide-toggler--active {
  background-color: $basic-purple;
}

.main-screen__title {
  margin-top: 0;
  margin-bottom: 20px;

  font-weight: 800;
  font-size: 48px;
  line-height: 60px;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: uppercase;
  word-spacing: 10px;

  & span {
    color: $basic-purple;
  }
}

.main-screen__subtitle {
  margin-bottom: 50px;

  font-weight: 400;
  font-size: 18px;
  color: $basic-light-grey;
}

.btn-purple {
  display: inline-block;
  padding: 20px 44px;

  font-size: 14px;
  color: $basic-white;
  text-decoration: none;

  background: linear-gradient(180deg, #8627e5 0%, #7821cf 100%);
  border: none;
  border-radius: 40px;
  box-shadow: 0 2px 15px 2px rgba(134, 39, 229, 0.2);

  &:hover {
    color: $basic-white;

    opacity: 0.8;
  }
}

.main-screen__text {
  margin-top: 80px;

  font-weight: 400;
  font-size: 18px;
  color: $basic-light-grey;

  & span {
    font-weight: 500;
    color: $basic-white;
  }
}

.advantages {
  margin: 0;
  padding: 20px 0;

  background: rgba(255, 255, 255, 0.04);
}

.slider {
  @include reset-list;
}

.advantages__list {
  @include reset-list;

  display: grid;

  grid-column-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr;
}

.advantages__list-item {
  display: flex;
  align-items: center;
}

.advantages__list-item-number {
  margin-top: 0;
  margin-right: 15px;
  margin-bottom: 0;

  font-weight: 700;
  font-size: 46px;
  font-family: "Panton", "Arial", sans-serif;
  color: $basic-purple;
}

.advantages__list-item-text {
  margin: 0;
  margin-bottom: 0;

  font-size: 16px;
  color: $basic-light-grey;
}

@media screen and (max-width: 1199px) {
  .slider__item-wrapper {
    width: 50%;
  }
}

@media screen and (max-width: 991px) {
  .main-screen {
    background-image: url("../img/main-bg-text.webp"), url("../img/main-bg-tablet.webp");
  }

  .slider__item-wrapper {
    width: 100%;
    min-height: 400px;
    padding-bottom: 330px;
  }

  .advantages__list-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .advantages__list-item-number {
    margin-right: 0;
  }

  .directions__subtitle {
    display: block;
    width: 80%;
    margin: 0 auto;
  }

  .slider__item {
    background-position: bottom center;
  }
}

@media screen and (max-width: 767px) {
  .slider__item-wrapper {
    padding-bottom: 300px;
  }

  .slider__item {
    background-position: bottom center;
    background-size: 100%;
  }

  .directions__subtitle {
    display: block;
    width: 90%;
    margin: 0 auto;
  }

  .advantages__list-item-number {
    font-size: 36px;
  }
}

@media screen and (max-width: 575px) {
  .advantages__list {
    display: none;
  }

  .slider__item-title {
    font-size: 20px;
  }

  .slider__item-subtitle {
    margin: 5px 0;

    font-size: 14px;
  }

  .slider__item-name {
    margin-bottom: 0;

    font-size: 14px;

    & span {
      font-size: 14px;
    }
  }

  .slider__item-wrapper {
    padding-bottom: 200px;
  }
}
